async function handleRandomSpot(url) {
  try {
    for (let index = 0; index < url.length; index++) {
      const element = url[index];
      const result = await getRandomSpot(element);
      if(index> -1 &&index<2){ // 0 1 2  
        setLayers(prevLayers=>{
          const newLayers = [...prevLayers];
          newLayers[0][index+1] = { ...newLayers[0][index+1], theme: changeToText(result?.cat3), image: result?.firstimage2, text:result?.title, contenttypeid:result?.contenttypeid,contentid:result?.contentid, active: false };
          return newLayers;
        })
      } else if(index>1 && index<5){ // 3 4 5
        setLayers(prevLayers=>{
          const newLayers = [...prevLayers];
          newLayers[1][index-2] = { ...newLayers[1][index-2], theme:changeToText(result?.cat3), image: result?.firstimage2, text:result?.title, contenttypeid:result?.contenttypeid,contentid:result?.contentid,active: false };
          return newLayers;
        })
      } else if(index>4 && index< 8) { // 6 7 8
        setLayers(prevLayers=>{
          const newLayers = [...prevLayers];
          newLayers[2][index-5] = { ...newLayers[2][index-5], theme:changeToText(result?.cat3), image: result?.firstimage2, text:result?.title, contenttypeid:result?.contenttypeid,contentid:result?.contentid,active: false };
          return newLayers;
        })
      }
    }
  } catch (err) {
    console.log("에러", err);
  }
}


